import URI from 'urijs';
var API_BASE = '/api/remediations/v1';

function checkResponse(r) {
  if (!r.ok) {
    var error = new Error("Unexpected response code ".concat(r.status));
    error.statusCode = r.status;
    throw error;
  }

  return r;
}

function json(r) {
  checkResponse(r);
  return r.json();
}

var headers = Object.freeze({
  'Content-Type': 'application/json; charset=utf-8'
});
export function createRemediation(data) {
  var base = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : API_BASE;
  var uri = new URI(API_BASE).segment('remediations').toString();
  return fetch(uri, {
    headers: headers,
    method: 'POST',
    credentials: 'include',
    body: JSON.stringify(data)
  }).then(json);
}
export function patchRemediation(id, data) {
  var base = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : API_BASE;
  var uri = new URI(API_BASE).segment('remediations').segment(id).toString();
  return fetch(uri, {
    headers: headers,
    credentials: 'include',
    method: 'PATCH',
    body: JSON.stringify(data)
  }).then(checkResponse);
}
export function getRemediations() {
  var basePath = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : API_BASE;
  var uri = new URI(API_BASE).segment('remediations').query({
    limit: 200
  }).toString();
  return fetch(uri, {
    credentials: 'include'
  }).then(json);
}
export function getRemediation(id) {
  var basePath = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : API_BASE;
  var uri = new URI(API_BASE).segment('remediations').segment(id).toString();
  return fetch(uri, {
    credentials: 'include'
  }).then(json);
}
export function getResolutionsBatch(issues) {
  var basePath = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : API_BASE;
  var uri = new URI(API_BASE).segment('resolutions').toString();
  return fetch(uri, {
    headers: headers,
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify({
      issues: issues
    })
  }).then(json);
}